import { Component, OnInit } from '@angular/core';

declare const $: any;
declare interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
    subMenu: SubMenu [];
    iconArrow : string;
    showMenu: string;
    maChucVu : string;
}
interface SubMenu {
    path :string;
    title : string;
    
}
export const ROUTES: RouteInfo[] = [
    { path: '/thongtincanhan', title: 'Thng tin c nhn',  icon: 'contact_mail', class: '' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '/quanlyuser', title: 'Qun l users',  icon:'person', class: 'qluser' , 
    subMenu : [
        {path : '/danhsachuser',title : 'Danh sch user'},
        {path : '/themuser',title : 'Thm user'}
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l kho st',  icon:'assignment', class: 'qlkhaosat' , 
    subMenu : [
        {path : '/phieukhaosat',title : 'Phiu kho st'},
        {path : '/ketquakhaosat',title : 'Kt qu kho st'}
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l sinh vin',  icon:'portrait', class: 'qlsv' , 
    subMenu : [
        {path : '/dssv',title : 'Danh sch sinh vin'},
        {path : '/themsv',title : 'Thm sinh vin'},
        {
            path : '/themdssv' , title : 'Thm danh sch sinh vin'
        }
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l trng ',  icon:'account_balance', class: 'qltruong' , 
    subMenu : [
        {path : '/danhtruong',title : 'Danh trng '},
        {path : '/themtruong',title : 'Thm trng'}
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l ngnh',  icon:'work', class: 'qlngangh' , 
    subMenu : [
        {path : '/danhnganh',title : 'Danh ngnh'},
        {path : '/themnganh',title : 'Thm ngnh'}
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    //
    { path: '/qllienhe', title: 'Qun l lin h',  icon: 'link', class: 'qllienhe' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '/thongke', title: 'Thng k',  icon: 'contact_mail', class: 'thongke' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '/baocao', title: 'Bao co',  icon: 'contact_mail', class: 'baocao' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
];
export const ROUTES2: RouteInfo[] = [
    { path: '/thongtincanhan', title: 'Thng tin c nhn',  icon: 'contact_mail', class: '' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l sinh vin',  icon:'portrait', class: 'qlsv' , 
    subMenu : [
        {path : '/dssv',title : 'Danh sch sinh vin'},
        {path : '/themsv',title : 'Thm sinh vin'},
        {
            path : '/themdssv' , title : 'Thm danh sch sinh vin'
        }
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l trng ',  icon:'account_balance', class: 'qltruong' , 
    subMenu : [
        {path : '/danhtruong',title : 'Danh trng '},
        {path : '/themtruong',title : 'Thm trng'}
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '', title: 'Qun l ngnh',  icon:'work', class: 'qlngangh' , 
    subMenu : [
        {path : '/danhnganh',title : 'Danh ngnh'},
        {path : '/themnganh',title : 'Thm ngnh'}
    ] ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    //
    { path: '/qllienhe', title: 'Qun l lin h',  icon: 'link', class: 'qllienhe' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
    { path: '/baocao', title: 'Bao co',  icon: 'contact_mail', class: 'baocao' , subMenu : null ,iconArrow : 'keyboard_arrow_down',showMenu : '0',maChucVu : '1'},
];
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  menuItems: any[];
  showMenu: string = '';
  iconMenu:string = 'keyboard_arrow_down';
  isActive: boolean = false;
  private MaChucVu:string = "-1";
  constructor() { }

  ngOnInit() {
   
    
    if (localStorage.getItem("MaChucVu") != null){
        this.MaChucVu = localStorage.getItem("MaChucVu");
        if (this.MaChucVu == "1"){
            this.menuItems = ROUTES.filter(menuItem => menuItem);
        }else if (this.MaChucVu == "2"){
            this.menuItems = ROUTES2.filter(menuItem => menuItem);
        }
    }
    console.log("this.MaChucVu = " + this.MaChucVu);
  }
  isMobileMenu() {
      if ($(window).width() > 991) {
          return false;
      }
      return true;
  };
  addExpandClass(element: string , index : number ) {
      console.log("element = " + element);
      console.log("index = " + index);
      console.log("this.menuItems[index].class = " + this.menuItems[index].class);
      console.log("element === this.menuItems[index].class =  " + (element === this.menuItems[index].class));
    if (element == this.menuItems[index].showMenu) {
        this.menuItems[index].iconArrow = 'keyboard_arrow_down';
        this.menuItems[index].showMenu = '0';
    } else {
        this.menuItems[index].showMenu = element;
        this.menuItems[index].iconArrow  = 'keyboard_arrow_up';
    }
}
}



